import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { AlertController, ModalController } from '@ionic/angular';
import { UsuariosLoginService } from '../../../providers/Api/UsuariosLogin.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Router } from '@angular/router';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { LoadingService } from '../../../providers/Utilidades/LoadingService.service';
import { ModalMapasPage } from '../modal-mapas/modal-mapas.page';

declare var mapboxgl;

@Component({
  selector: 'app-grua',
  templateUrl: './grua.page.html',
  styleUrls: ['./grua.page.scss'],
})
export class GruaPage implements OnInit {

  lat = 0;
  lng = 0;

  mensaje = '';

  imagen = '';

  @ViewChild('mapa') mapa: ElementRef;

  constructor(
    private alertCtrl: AlertController,
    private geoReferencia: Geolocation,
    private usuaripService: UsuariosLoginService,
    private almacenamiento: InicioService,
    private statusbar: StatusBar,
    private router: Router,
    private toast: ToastServices,
    private loading: LoadingService,
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {

 
  }

  ionViewWillEnter() {
    this.statusbar.backgroundColorByHexString('#273238');
    this.llamarFoto();
    this.confirmarUbicacion();
  }

  async confirmarUbicacion() {
    const alert = await this.alertCtrl.create({
      header: 'Mensaje de la App',
      message: 'Permitir compartir la ubciaci贸n para realizar esta acci贸n',
      backdropDismiss: false,
      buttons: [
        {
          role: 'cancel',
          text: 'Cancelar',
          handler: () => {
            this.toast.toastNotificacion('No se puede realizar la acci贸n debido a no compartir la ubicaci贸n', 3000)
            this.router.navigate(['/central/menu/inicioMapa']);
          }
        },
        {
          text: 'Permitir',
          handler: () => {
            this.calcular();
          }
        }
      ]
    })

    await alert.present();
  }

  async calcular() {
    try {
      console.log('entramos al click')
      const data = await this.geoReferencia.getCurrentPosition();

      if ( data ) {
      console.log(data);
      this.lat = data.coords.latitude;
      this.lng = data.coords.longitude;
      this.graficar();
    }
    } catch (error) {
      
      this.mensaje = error;
    }
  }

  
async llamarFoto() {

  const info = await this.almacenamiento.obtenerToken();

  if ( info ) {
    this.usuaripService.viewImagen(info.token).subscribe((url: any) => {
      // // console.log(url);
      this.imagen = `https://motocaliapp.com/${url.paquete}`;
      console.log(this.imagen)
    }, (error) => {
      // // console.log(error);
    })
  }
      
    }

  graficar() {
    mapboxgl.accessToken = 'pk.eyJ1Ijoic2FudGlhZ29mZXJuYW5kZXpkZXYiLCJhIjoiY2tmYmh6cXdzMDM0bDJ2cHRucTI4cHpkaSJ9.-01EYvsLdJOU1Oy4BtwBwA';
    const mapa = new mapboxgl.Map({
    container: this.mapa.nativeElement,
    style: 'mapbox://styles/mapbox/streets-v11',
    center: [this.lng, this.lat],
    zoom: 15
    });

    mapa.addControl(new mapboxgl.NavigationControl());

    const el = document.createElement('div'); 
    el.className = 'marker'; 
    el.style.backgroundImage = 'url(' + this.imagen + ')';
    el.style.backgroundSize = '100%';
    el.style.width = 50 + 'px';
    el.style.height = 50   + 'px';

    const marker = new mapboxgl.Marker({
      element: el,
      draggable: true
    })
      .setLngLat([this.lng, this.lat])
      .addTo(mapa)

      const el2 = document.createElement('div'); 
    el2.className = 'marker'; 
    el2.style.backgroundImage = 'url(' + this.imagen + ')';
    el2.style.backgroundSize = '100%';
    el2.style.width = 50 + 'px';
    el2.style.height = 50   + 'px';

    marker.on('dragend', () => {
          const lngLat = marker.getLngLat();
          console.log('Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat)
        });


    const marker2 = new mapboxgl.Marker({
          element: el2,
          draggable: true
        })
          .setLngLat([-76.49279796685475, 3.4727944759127922])
          .addTo(mapa)
    
        marker2.on('dragend', () => {
              const lngLat = marker2.getLngLat();
              console.log('Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat)
            });
    
  }

  async modal() {

    const modal = await this.modalCtrl.create({
      component: ModalMapasPage,
      componentProps: {
        
      }
    });

    await modal.present();

  }

  ionViewWillLeave(){
    this.statusbar.backgroundColorByHexString('#777777');
  }

}
